const formMethod = { //
    errorStyle : {  //
        right : {  //  
            showError : (obj)=>{
                var nativeEle = obj.nativeEle;
                var nextSibling = nativeEle.nextSibling;
                var parentNode = nativeEle.parentNode;

                var oEle = document.createElement('div');
                oEle.className = 'form-error-box';
                oEle.style.display = 'block';
                oEle.innerHTML = '<div class="form-tip"><span class="error-icon"></span><div class="tip-text">'+obj.errorMsg+'</div></div>';

                if(nextSibling){ //input
                    if(Utils.hasClass(nextSibling,'form-error-box')){  //
                        var formErrorBox = parentNode.getElementsByClassName('form-error-box')[0];
                        var tipText = formErrorBox.getElementsByClassName('tip-text')[0];
                        tipText.innerHTML = obj.errorMsg;
                        formErrorBox.style.display = 'block';
                    }else{
                        parentNode.insertBefore(oEle,nextSibling);
                    }
                }else{
                    parentNode.appendChild(oEle);
                }
            },
            hideError : (obj)=>{
                var parentNode = null;
                if(obj.target){
                    parentNode = obj.target.parentNode;
                }else{
                    parentNode = obj.parentNode;
                }
                var allEle = parentNode.getElementsByClassName('form-error-box')[0];
                if(allEle){
                    allEle.style.display='none';
                }
            }
        }
    },
    messageCode(that,errorStyle){   //
        console.log(that);
        var selOjb = that.refs.getVerify;   //

        //
        that.refs.validator.validateOne('message_code');

        var isOkIphone = that.refs.validator.validateOne('iphone');
        if(!isOkIphone)return false;  //
        if(!that.state.form.isVerifyOpen)return false;

        that.state.form.isVerifyOpen = false;

        formMethod.errorStyle.right.hideError(selOjb);

        //
        var timer = null;
        var sec = 60;
        selOjb.innerHTML = ''+sec+'s';
        timer = setInterval(function () {
            sec--;
            if(sec<0){
                sec = 0;
                clearInterval(timer);
                selOjb.innerHTML = '';
                that.state.isVerifyOpen = true;
                componentStore.update(that,that.state);
            }else{
                selOjb.innerHTML = ''+sec+'s';
            }

        },1000);

        setTimeout(function () { //ajax
            that.state.form.message_from_server = 8888;
            componentStore.update(that,that.state);
        },200)
    }

}

export default formMethod;


